.megaregister{
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url("../Homepage/img/pattern.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
}

.registerpage-container {
    background: #343541;
    border-radius: 4px;
    box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.2);
    max-width: 615px;
    padding-bottom: 66px;
    padding-top: 46px;
    text-align: center;
    width: 90%;
    position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
  }
  
  .registerpage-container > .logo {
    max-width: 90%;
  }
  .registerpage-container > .form {
    margin-bottom: 98px;
    margin-top: 41px;
  }
  
  .registerpage-container .input-group {
    margin-bottom: 26px;
  }
  .registerpage-container .input-group:nth-last-child(-n + 2) {
    margin-bottom: 50px;
  }
  .registerpage-container .input-group > label,
  .input-group > input {
    font-family: 'Roboto', sans-serif;
    max-width: 420px;
    text-align: left;
    width: 80%;
  }
  .registerpage-container .input-group > label {
    color: #676767;
    display: inline-block;
    margin-bottom: 5px;
    text-transform: uppercase;
  }
  .registerpage-container .input-group > input {
    background: #f2f2f5;
    border-radius: 4px;
    border: 1px solid #e5e5e5;
    color: #676767;
    font-size: 16px;
    height: 40px;
    outline: 0;
    padding: 0 15px;
    transition: all 0.3s;
  }
  .registerpage-container .input-group > input:focus {
    background: #fff;
    border-color: #c4c4c4;
  }
  .registerpage-container .input-group > input::placeholder {
    color: #c4c4c4;
    font-size: 16px;
  }
  
  .registerpage-container button {
    border-radius: 5px;
    border: none;
    color: #fff;
    cursor: pointer;
    font-family: 'Open Sans', sans-serif;
    font-size: 18px;
    font-weight: bold;
    letter-spacing: 1px;
    max-width: 420px;
    outline: 0;
    padding: 10px 0;
    transition: background 0.3s;
    width: 80%;
  }
  .registerpage-container button.primary {
    background: #f58733;
  }
  .registerpage-container button.primary:hover {
    background: #ea6c0b;
  }
  .registerpage-container button.secondary {
    background: #c1d7aa;
  }
  .registerpage-container button.secondary:hover {
    background: #a8c787;
  }
  